<template>
  <div class="Menu">
    <div
      class="Btn Menu__toggle"
      @click="toggleMenu">
      <span/>
    </div>
    <ul
      v-show="isMenuVisible"
      class="Menu__dropdown">
      <li @click="goTo('home')">
        Actions
      </li>
      <li @click="goTo('inventory')">
        Backpack ({{ length }}/{{ maxLength }})
      </li>
      <li @click="goTo('crafting')">
        Crafting
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { MAXINVENTORY } from '@/data/constants'

export default {
  data () {
    return {
      isMenuVisible: false
    }
  },
  computed: {
    ...mapState(['inventory']),
    length () {
      return this.inventory.length
    },
    maxLength () {
      return MAXINVENTORY
    }
  },
  methods: {
    ...mapMutations(['changePage']),
    toggleMenu () {
      this.isMenuVisible = !this.isMenuVisible
    },
    goTo (newPage) {
      this.changePage({ newPage })
      this.toggleMenu()
    }
  }
}
</script>

<style lang="scss">
  @import '../assets/styles/variables';

  .Menu {
    position: absolute;
    left: 1em;
    top: 50%;
    margin-top: -1.35em;
    @media screen and (min-width: 680px) {
      display: none;
    }
    &__toggle {
      width: 2.5em;
      height: 2.5em;
      padding: .7em .5em;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      span,
      &:before,
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background: #000;
      }
    }
    &__dropdown {
      min-width: 50%;
      position: fixed;
      left: 0;
      top: 4em;
      padding: 0 1em;
      background: $lightColor;
      li {
        margin: 0;
        padding: .8em 0;
        border-bottom: 1px solid $darkColor;
        &:last-child {
          border-bottom: none;
        }
      }
    }
  }
</style>



